is.$el.firstElementChild,\n                    initialValue: this.value,\n                    renderingConfig: {},\n                    toolbar: [\n                        {\n                            name: \"bold\",\n                            action: SimpleMDE.toggleBold,\n                            className: \"mdi mdi-format-bold\"\n                        },\n                        {\n                            name: \"italic\",\n                            action: SimpleMDE.toggleItalic,\n                            className: \"mdi mdi-format-italic\"\n                        },\n                        {\n                            name: \"strikethrough\",\n                            action: SimpleMDE.toggleStrikethrough,\n                            className: \"mdi mdi-format-strikethrough-variant\"\n                        }, \"|\",\n                        {\n                            name: \"quote\",\n                            action: SimpleMDE.toggleBlockquote,\n                            className: \"mdi mdi-format-quote-close\"\n                        },\n                        {\n                            name: \"unordered-list\",\n                            action: SimpleMDE.toggleUnorderedList,\n                            className: \"mdi mdi-format-list-bulleted\"\n                        },\n                        {\n                            name: \"ordered-list\",\n                            action: SimpleMDE.toggleOrderedList,\n                            className: \"mdi mdi-format-list-numbers\"\n                        }, \"|\",\n                        {\n                            name: \"link\",\n                            action: SimpleMDE.drawLink,\n                            className: \"mdi mdi-link\"\n                        },\n                        {\n                            name: \"horizontal-rule\",\n                            action: SimpleMDE.drawHorizontalRule,\n                            className: \"mdi mdi-minus\"\n                        }, \"|\", \"side-by-side\",\n                        {\n                            name: \"preview\",\n                            action: SimpleMDE.togglePreview,\n                            className: \"mdi mdi-eye no-disable\"\n                        },\n                        {\n                            name:\"fullscreen\",\n                            action: SimpleMDE.toggleFullScreen,\n                            className: \"mdi mdi-fullscreen no-disable no-mobile\"\n                        }],\n                }, this.configs);\n\n                // Initial Input\n                if (configs.initialValue) {\n                    this.$emit('input', configs.initialValue);\n                }\n\n                // Should we highlight code\n                if (this.highlight) {\n                    configs.renderingConfig.codeSyntaxHighlighting = true;\n                }\n\n                // sanitize html?\n                marked.setOptions({sanitize: this.sanitize});\n\n                // Set simplemde\n                this.simplemde = new SimpleMDE(configs);\n\n                const textEditor = new CodeMirrorEditor(this.simplemde.codemirror);\n                const textcomplete = new Textcomplete(textEditor);\n                textcomplete.register([{\n                    // User match\n                    match: /(^|\\s)@(\\w.+)$/,\n                    search: function (term, callback) {\n                        // TODO: Finish this callback\n                        callback(function () {\n                            return ['Matt', 'Torrey']\n                        })\n                    },\n                    replace: function (value) {\n                        return '@' + value;\n                    }\n                }, {\n                    // Ticket match\n                    match: /(^|\\s)#(\\w.+)$/,\n                    search: function (term, callback) {\n                        console.log(term);\n                        // TODO: Finish this callback\n                        callback(function () {\n                            return ['AABBCCDD', 'ACFG1345A']\n                        })\n                    },\n                    replace: function (value) {\n                        return '#' + value;\n                    }\n                }, {\n                    // KB Match\n                    match: /(^|\\s)!(\\w.+)$/,\n                    search: function (term, callback) {\n                        // TODO: Finish this callback\n                        callback(function () {\n                            return ['AAVVGGHH', 'TEST1234']\n                        })\n                    },\n                    replace: function (value) {\n                        return '!' + value;\n                    }\n                }]);\n\n                // set previewClass\n                const className = this.previewClass || '';\n                this.addPreviewClass(className);\n\n                // Bind events\n                /*this.simplemde.codemirror.on(\"change\", function (instance, changeObj) {\n                    textcomplete.trigger(this.simplemde.value());\n                });*/\n                this.bindingEvents();\n            },\n            bindingEvents() {\n                this.simplemde.codemirror.on('change', () => {\n                    this.$emit('input', this.simplemde.value());\n                });\n            },\n            addPreviewClass(className) {\n                const wrapper = this.simplemde.codemirror.getWrapperElement();\n                const preview = document.createElement('div');\n                wrapper.nextSibling.className += ` ${className}`;\n                preview.className = `editor-preview ${className}`;\n                wrapper.appendChild(preview);\n            },\n        },\n        destroyed() {\n            this.simplemde = null;\n        },\n        watch: {\n            value(val) {\n                if (val === this.simplemde.value()) return;\n                this.simplemde.value(val);\n            },\n        },\n    };\n</script>\n\n<style>\n    @import '~simplemde/dist/simplemde.min.css';\n\n    .markdown-editor .markdown-body {\n        padding: 0.5em\n    }\n\n    .markdown-editor .editor-preview-active, .markdown-editor .editor-preview-active-side {\n        display: block;\n    }\n</style>"],"sourceRoot":""}]);

